import 'babel-polyfill';


import React from 'react'
import { render } from 'react-dom'
//import { Router, Route } from 'react-router-dom';
//import { Router, Route } from 'react-router'
import App from './modules/App'
import about from './modules/about'
import { Link } from 'react-router-dom';
// import { Router, Route, hashHistory } from 'react-router-dom'
import { Switch, BrowserRouter, Route, Redirect } from 'react-router-dom';

//import {Component} from 'react'
// console.log(window.location.hash)
// console.log(window.location.pathname)
let isactive1 = true

let isactive2 = true
if (window.location.hash == "#/") {
    isactive1 = true
    isactive2 = false

} else if (window.location.hash == "#/about") {
    isactive2 = true
    isactive1 = false
} else {
    isactive1 = true
    isactive2 = false
}

const Apphome = () => (
    <BrowserRouter         basename={window.location.pathname + "#/"}      forceRefresh={false}         keyLength={12}     >
        <div>

            <nav class="navbar navbar-default" role="navigation"  >
                <div class="container-fluid">
                    <div class="navbar-header">

                        <a class="navbar-brand ">masx200的github</a>
                    </div>
                    <div>
                        <ul class="nav navbar-nav">
                            <li class={isactive1 ? "active" : ""} onClick={handleClick1}>

                                <Link to="/">基于REACT的主页</Link>
                            </li>

                            <li class={isactive2 ? "active" : ""} onClick={handleClick2}>

                                <Link to="/about">关于</Link>
                            </li>

                            <li>
                                <a href="../index.html">传统网页</a>
                            </li>

                        </ul>
                    </div>
                </div>
            </nav>


            <Switch>
                <Route exact path="/" component={App} />
                <Route path="/about" component={about} />
                <Redirect from='*' to='/' />
            </Switch>
        </div>
    </BrowserRouter>
)
function handleClick1() {
    // console.log(this.props.match.params)
    // console.log(window.location.hash)
    isactive1 = true
    isactive2 = false
    render((
        <Apphome />
    ), document.getElementById('root'))
}
function handleClick2() {
    // console.log(this.props.match.params)
    // console.log(window.location.hash)
    isactive2 = true
    isactive1 = false
    render((
        <Apphome />
    ), document.getElementById('root'))
}

render((
    <Apphome />
), document.getElementById('root'))